<template>
  <div class="resource-detail">
    <div class="detail-header">
      <button class="back-button" @click="$emit('back')">返回列表</button>
      <h1>{{ resource.title }}</h1>
    </div>
    <img :src="titleImageUrl" :alt="resource.title" class="detail-image">
    <div class="content" v-html="content"></div>
  </div>
</template>

<script>
export default {
  name: 'ResourceDetail',
  props: {
    resource: {
      type: Object,
      required: true
    }
  },
  data(){
    return {
        content: '',
        titleImageUrl: this.resource.titleImageURL
    }
  },
  mounted(){
    
      this.fetchContent(this.resource.contentURL, this.resource.contentID)
  },
  methods:{
    async fetchContent(contentURL, contentID) {
        try{
            const response = await fetch(contentURL)
            const contentArr = await response.json()
            
            for(let i = 0; i < contentArr.length; i++){
                if(contentArr[i].contentID == contentID){
                    this.content = contentArr[i].content;
                }
            }
        } catch(error){
            console.log(error)
        }
        return this.content;
    }
  }
}
</script>

<style scoped>
.resource-detail {
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.detail-header {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.back-button {
  padding: 8px 16px;
  margin-right: 20px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
}

.back-button:hover {
  background-color: #f5f5f5;
}

.detail-image {
  width: 100%;
  max-height: 400px;
  object-fit: cover;
  border-radius: 8px;
  margin-bottom: 20px;
}

.content {
  line-height: 1.8;
  color: #333;
}

.content :deep(img) {
  max-width: 100%;
  height: auto;
}

.content :deep(p) {
  margin: 1em 0;
}
</style>