<template>
  <div class="resource-page">
    <!-- 条件渲染列表或详情 -->
    <template v-if="selectedResource">
      <ResourceDetail 
        :resource="selectedResource"
        @back="selectedResource = null"
      />
    </template>
    <template v-else>
      <!-- 资源列表 -->
      <div class="resource-grid">
        <ResourceCard
          v-for="item in currentPageItems"
          :key="item.id"
          :resource="item"
          @click-resource="showResourceDetail"
        />
      </div>

      <!-- 分页控件 -->
      <div class="pagination">
        <button 
          :disabled="currentPage === 1" 
          @click="changePage(currentPage - 1)"
          class="page-btn"
        >
          上一页
        </button>
        <span class="page-info">{{ currentPage }} / {{ totalPages }}</span>
        <button 
          :disabled="currentPage === totalPages" 
          @click="changePage(currentPage + 1)"
          class="page-btn"
        >
          下一页
        </button>
      </div>
    </template>
  </div>
</template>

<script>
import ResourceCard from '@/components/ResourceCard.vue'
import ResourceDetail from '@/components/ResourceDetail.vue'

export default {
  name: 'ResourceView',
  components: {
    ResourceCard,
    ResourceDetail
  },
  data() {
    return {
      resources: [],
      currentPage: 1,
      pageSize: 10,
      selectedResource: null
    }
  },
  computed: {
    totalPages() {
      return Math.ceil(this.resources.length / this.pageSize)
    },
    currentPageItems() {
      const start = (this.currentPage - 1) * this.pageSize
      const end = start + this.pageSize
      return this.resources.slice(start, end)
    }
  },
  methods: {
    async fetchResources() {
      try {
          const articlesResponse = await fetch('https://raw.githubusercontent.com/3542Q/JSRepository/main/articles/articles.json')
          const articles = await articlesResponse.json(); // 解析json数据
          
          /*articles是一个从数据库里获取到的数组,其元素的字段如下：
            CREATE TABLE Article (
            articleID INT AUTO_INCREMENT PRIMARY KEY,
            title VARCHAR(255),
            titleImageURL TEXT,
            contentID INT,
            contentURL TEXT,
          );*/
          articles.sort((a, b) => b.articleID - a.articleID); // 按articleID降序排序，以便获取最新的文章
          this.resources = articles;
      } catch (error) {
          console.error('Failed to load script:', error)
      }
    },
    changePage(page) {
      if (page >= 1 && page <= this.totalPages) {
        this.currentPage = page
      }
    },
    showResourceDetail(resource) {
      this.selectedResource = resource
    }
  },
  mounted() {
    this.fetchResources()
  }
}
</script>

<style scoped>
.resource-page {
  padding: 20px;
  max-width: 1400px;
  margin: 0 auto;
}

.resource-grid {
  display: grid;
  grid-template-columns: repeat(5, 1fr); /* 固定5列 */
  grid-auto-rows: min-content; /* 行高自适应内容 */
  gap: 20px;
  margin-bottom: 30px;
}

/* 强制每行只能放置5个项目 */
.resource-grid > :nth-child(5n + 6) {
  grid-column: 1;
}

/* 限制最多显示两行 */
.resource-grid > :nth-child(n + 11) {
  display: none;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}

.page-btn {
  padding: 8px 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  background-color: #fff;
  cursor: pointer;
}

.page-btn:disabled {
  background-color: #f5f5f5;
  cursor: not-allowed;
}

.page-btn:hover:not(:disabled) {
  background-color: #f5f5f5;
}

.page-info {
  font-size: 14px;
  color: #666;
}
</style>